<template>
  <div class="scrollWrap" ref="scrollWrap">
    <div class="srollContent">
      <slot/>
    </div>
    <to-top @click.native="toTop()" v-show="toTopShow" v-if="toTopConfigDefault.toTop"/>
  </div>
</template>

<script>
import BScroll from 'better-scroll';
import ToTop from './ToTop'

export default {
  name: 'Scroll',
  components: {
    ToTop
  },
  data() {
    return {
      toTopConfigDefault: {
        toTop: true,
        time: 1000,
        toTopShpw: -1000
      },
      toTopShow: false
    }
  },
  props: {
    //  
    toTopConfig: {
      toTop: Boolean,
      time: Number,
      toTopShow: Number
    },

    /**
     * 1 scroll
     * 2 scroll
     * 3 scrollswipescroll
     */
    probeType: {
      type: Number,
      default: 0
    },
    /**
     * click
     */
    click: {
      type: Boolean,
      default: true
    },
    /**
     * 
     */
    scrollX: {
      type: Boolean,
      default: false
    },
    /**
     * 
     */
    listenScroll: {
      type: Boolean,
      default: false
    },
    /**
     * 
     */
    data: {
      type: Array,
      default: null
    },
    /**
     * 
     */
    pullup: {
      type: Boolean,
      default: false
    },
    /**
     * 
     */
    pulldown: {
      type: Boolean,
      default: false
    },
    /**
     * 
     */
    beforeScroll: {
      type: Boolean,
      default: false
    },
    /**
     * scroll
     */
    refreshDelay: {
      type: Number,
      default: 20
    },

    //
    bounce: {
      type: Boolean,
      default: true
    },

    scrollbar: {
      type: Boolean | Object,
      default: false
    }
  },
  mounted() {
    // console.log(scroll)
    // this.$nextTick(() => {
    //   this.scroll = new BScroll(this.$refs.scrollWrap,{})
    // })
    this.$nextTick(() => {
      this.initScroll()
      this.initToTop()
    })
    
  },
  methods: {
    initScroll() {
      if(!this.$refs.scrollWrap) {
        return
      }

      this.scroll = new BScroll(this.$refs.scrollWrap,{
        probeType: this.probeType,
        click: this.click,
        scrollX: this.scrollX,
        bounce: this.bounce,
        scrollbar: this.scrollbar,
        // disableMouse: false
        mouseWheel: true
      })

      // 
      if (this.listenScroll) {
        this.scroll.on('scroll', (pos) => {
          this.$emit('scroll', pos)
        })
      }

      // 
      if (this.pullup) {
        this.scroll.on('scrollEnd', () => {
          // 
          if (this.scroll.y <= (this.scroll.maxScrollY + 50)) {
            this.$emit('scrollToEnd')
          }
        })
      }

      // 
      if (this.pulldown) {
        this.scroll.on('touchend', (pos) => {
          // 
          if (pos.y > 50) {
            this.$emit('pulldown')
          }
        })
      }

      // 
      if (this.beforeScroll) {
        this.scroll.on('beforeScrollStart', () => {
          this.$emit('beforeScroll')
        })
      }

      
    },

    // 
    initToTop() {
      // 
      if(this.toTopConfig) {
        Object.assign(this.toTopConfigDefault,this.toTopConfig)
        
        this.scroll.on('scroll', (pos) => {
          if( pos.y < this.toTopConfigDefault.toTopShpw ){
            this.toTopShow = true
          }else{
            this.toTopShow = false
          }
        })
      }
    },

    disable() {
      // better-scrolldisable
      this.scroll && this.scroll.disable()
    },
    enable() {
      // better-scrollenable
      this.scroll && this.scroll.enable()
    },
    refresh() {
      // better-scrollrefresh
      this.scroll && this.scroll.refresh()
    },
    scrollTo() {
      // better-scrollscrollTo
      this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
    },
    scrollToElement() {
      // better-scrollscrollToElement
      this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
    },

    finishPullUp() {
      //   
      this.scroll && this.scroll.finishPullUp()
    },


    // 
    toTop() {
      this.scroll.scrollTo(0,0,this.toTopConfigDefault.time)
      this.toTopShow = false
    },
  },
  watch: {
    data() {
      setTimeout(() => {
        this.refresh()
      },this.refreshDelay)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.scrollWrap{
  /* height: 100%; */
  /* height: 500px; */
  /* overflow: scroll; */
  overflow: hidden
}
</style>
